export default function (props: any) {
  let markPoint 
  if (props.marks) {
    if (props.marks.type === 'max-min') {
      markPoint = {
        data: [{
          type: 'max',
          name: ''
        }, {
          type: 'min',
          name: ''
        }]
      }
    }
  }
  return {
    markPoint
  }
}