/**
 * 
 *  require.context
 * @param dir 
 */
function pack (context: any): {
    [key: string]: any
  } {
  let result: { [name: string]: any } = {}
  context.keys().forEach((p: string) => {
    let name = (p.match(/\.\/(\w+)\.ts$/) || ['', 'null'])[1]
    result[name] = context(p)['default']
  })
  return result
}

export default pack