export default (props: any, providerOptions: any) => {
  let tooltip = props.accessories.tooltip
  let output = {
    ...providerOptions.tooltip,
    padding: 0,
    backgroundColor: 'transparent',
    show: true,
    trigger: props.type==='pie'?'item':'axis',
    positon: tooltip.positon,
    confine:true
  }
  if (tooltip.formatter) {
    output.borderWidth = 0
    output.extraCssText = ''
    output.formatter = [
      '<div class="tooltip">',
      tooltip.formatter,
      '</div>'
    ].join('')
  }
  if (tooltip.data) {
    // tooltipdata
    // tooltipdatchartdata
    output.formatter =
      (params: any) => {
        let dataIndex = params[0].dataIndex // X
        let legend = Array.isArray(props.legend)
          ? props.legend
          : props.legend.data
        let content = tooltip.data.map((line: any[], x: number) => [
          params.axisValueLabel,
          legend[x],
          ': ',
          line[dataIndex]
        ].join('')
        ).join('<br>')
        return [
          '<div class="tooltip">',
          params[0].axisValueLabel,
          '<br>',
          content,
          '</div>'
        ].join('')
      }
  }
  // tooltip
  if (tooltip.axisPointer) {
    output.axisPointer = {
      type: 'cross',
      lineStyle: {
        type: 'dotted'
      }
    }
  }
  return {
    tooltip: output
  }
}
