import { StyleRules } from '../../../src/core/shared/styles'

/**
 * 
 */
namespace PropTypes {
  export abstract class PropValueType {
    abstract valueOf (): any
  }

  /**
   * 
   */
  export class Dimension extends PropValueType {
    width: number | string | undefined
    height: number | string | undefined
    constructor (input: {
      width: number | string,
      height: number | string,
    }) {
      super()
      this.width = input.width
      this.height = input.height
    }

    valueOf () {
      return [this.width, this.height]
    }
  }

  /**
   * 
   */
  export class Position extends PropValueType {
    x: number | string
    y: number | string
    z?: number | string
    constructor (input: {x: number, y: number, z: number}) {
      super()
      this.x = input.x
      this.y = input.y
      this.z = input.z
    }

    valueOf () {
      return this.z? [this.x, this.y, this.z]: [this.x, this.y]
    }
  }

  /**
   * 
   */
  export class Percent extends PropValueType {
    number: number | string
    constructor (number: number | string) {
      super()
      if (typeof number === 'string') {
        number = parseInt(number, 10)
      }
      this.number = number
    }

    valueOf () {
      return this.number + '%'
    }
  }

  /**
   * 
   */
  export class Range extends PropValueType {
    value?: [number, number]
    constructor (input: [number, number] | [string, string]) {
      super()
      this.value = input ?
        [parseInt(input[0] + '', 10), parseInt(input[1] + '', 10)]
        : undefined
    }

    valueOf () {
      return this.value
    }
  }

  /**
   * Json
   */
  export class Json extends PropValueType {
    value: Object
    constructor (input: Object) {
      super()
      this.value = input
    }

    valueOf () {
      return this.value
    }
  }

  /**
   * 
   */
  export class Styles extends PropValueType {
    value: StyleRules
    constructor (input: StyleRules) {
      super()
      this.value = input
    }

    valueOf () {
      return this.value
    }
  }

  /**
   * 
   */
  export class Theme extends PropValueType {
    name: string
    constructor (input: string) {
      super()
      this.name = input
    }

    valueOf () {
      return this.name
    }
  }

  /**
   * 
   */
  export class Colors extends PropValueType {
    value?: string[]
    constructor (input: string[]) {
      super()
      this.value = input
    }

    valueOf () {
      return this.value
    }
  }

  /**
   * 
   */
  export class Select extends PropValueType {
    value?: string
    constructor (input: string) {
      super()
      this.value = input
    }

    valueOf () {
      return this.value
    }
  }

  /**
   * 
   */
  export class Size extends PropValueType {
    value?: string
    constructor (input: string) {
      super()
      this.value = input
    }

    valueOf () {
      return this.value
    }
  }
}

export default PropTypes
